body {
  font-family: 'Roboto', sans-serif;
}
.container {
  padding: 0 15px;
  margin: 0 auto;
  width: 100%;
  background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)),
    linear-gradient(blue 50%, yellow 50%);
}
.search-form {
  display: flex;
  height: 50px;
  justify-content: center;
  align-items: center;
}
button {
  color: #212121;
  height: 30px;
  font-size: 15px;
  letter-spacing: 0.06em;
  border-radius: 4px;
  border: none;
  
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  }
}
input {
  border-radius: 4px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid white;
  font-size: 16px;
  height: 30px;
  margin-right: 5px;
  padding: 0 10px;
 
  outline: none;

  &:hover{
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  }


  &:focus {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border: 2px solid gray;
  }
}
